  fetchHtml = () => {
    const data = { ...this.state };
    delete data['tableHead'];
    delete data['tableTitle'];
    delete data['bangcode'];
    delete data['htmlContent'];
    delete data['AdditionalWageRate'];
    const value4 = new Array();
    if (data.checkedItems.has('')) value4.push(1);
    if (data.checkedItems.has('')) value4.push(2);
    if (data.checkedItems.has('')) value4.push(3);
    if (data.checkedItems.has('')) value4.push(4);
    // data['value4'] = (data.value4.length > 0)? data.value4 : value4;
    data['value4'] = value4;
    delete data['checkedItems'];

    data.types1 = [
      { label: '   ', value: 0 },
      { label: '', value: 1 },
    ];
    data.types2 = [
      { label: '   ', value: 0 },
      { label: '', value: 1 },
    ];
    data.types3 = [
      { label: '    ', value: 0 },
      { label: '   ', value: 1 },
    ];

    data.BusinessName = this.props.userinfo.business_name;

    console.log(data.value4);
    delete data.signOrStamp;

    writeContractform(data).then((res) => {
      selectBusinessByName(this.state.bangcode)
        .then((result) => result.json())
        .then((result) => {
          try {
            sendMessage(
              result[0].id,
              null,
              this.state.id,
              null,
              `<${this.state.bang}> ${this.state.id}  . [>] .`,
              null,
              1,
              3,
            ).then((r) => {
              this.props.history.push('/workerManage');
            });
          } catch (e) {
            console.error(e);
          }
        });
    });
  };

